@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;600;700;800;900&display=swap);
:root {
    --dark-Blue-Dark-Mode-Elements: hsl(209, 23%, 22%);
    --Very-Dark-Blue-Dark-Mode-Background: hsl(207, 26%, 17%);
    --Very-Dark-Blu-Light-Mode-Text: hsl(200, 15%, 8%);
    --Dark-Gray-Light-Mode-Input: hsl(0, 0%, 52%);
    --Very-Light-Gray-Light-Mode-Background: hsl(0, 0%, 98%);
    --White: hsl(0, 0%, 100%);
}

*,
*::before,
*::after {
    font-size: 14px;
    font-family: 'Nunito Sans', sans-serif;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    min-height: 100vh;
}
main {
    min-height: 100vh;
    background-color: var(--Very-Dark-Blue-Dark-Mode-Background);
}

.light {
    background-color: var(--Very-Light-Gray-Light-Mode-Background);
}

.light header {
    background-color: var(--White);
}

.light header h1,
.light header button {
    color: #000;
}

header {
    background-color: var(--dark-Blue-Dark-Mode-Elements);
    display: flex;
    padding: 2rem 6rem;
    box-shadow: 0 0 5px 0px rgba(0, 0, 0, .5);
}

header h1 {
    color: var(--White);
    font-size: 2rem;
}

header button {
    margin-left: auto;
    cursor: pointer;
    border: none;
    outline: none;
    background-color: transparent;
    color: var(--White);
    font-size: 1.25rem;
    font-weight: 700;
    display: flex;
    align-items: center;
}

header button span .moon {
    font-size: 1.5rem;
    margin-right: 0.5rem;
    transform: translateY(.15rem);
}

.container {
    margin: 0 6rem;
}

@media screen and (max-width: 600px) {
    .container {
        margin: 0 2rem;
    }
    header {
        padding: 2rem;
    }
    header h1 {
        font-size: 1.25rem;
    }
    header button {
        font-size: 1rem;
    }
}
.input-container {
    margin: 4rem 0;
    display: flex;
    justify-content: space-between;
}

.search {
    width: 35rem;
    background-color: var(--dark-Blue-Dark-Mode-Elements);
    box-shadow: 0 1px 5px 0px rgba(0, 0, 0, .4);
    display: flex;
    align-items: center;
    border-radius: 5px;
}

.light .search {
    background-color: var(--White);
}

.light ::-webkit-input-placeholder {
    color: black !important;
}

.light :-ms-input-placeholder {
    color: black !important;
}

.light .search input,
.light .search-icon,
.light ::placeholder {
    color: black !important;
}

.light :-ms-input-placeholder,
.light ::-ms-input-placeholder {
    color: black;
}

.search input {
    width: 85%;
    height: 100%;
    padding: 1.25rem;
    background-color: transparent;
    border: none;
    outline: none;
    color: white;
    font-weight: 600;
}

.search-icon {
    margin: 0 0rem 0 2.5rem;
    color: white;
    font-size: 2rem;
}

::-webkit-input-placeholder {
    color: white;
    font-weight: 600;
    font-family: 'Nunito Sans', sans-serif;
    opacity: 1;
}

:-ms-input-placeholder {
    color: white;
    font-weight: 600;
    font-family: 'Nunito Sans', sans-serif;
    opacity: 1;
}

::placeholder {
    color: white;
    font-weight: 600;
    font-family: 'Nunito Sans', sans-serif;
    opacity: 1;
}

 :-ms-input-placeholder {
    color: white;
    font-weight: 600;
    font-family: 'Nunito Sans', sans-serif;
}

 ::-ms-input-placeholder {
    color: white;
    font-weight: 600;
    font-family: 'Nunito Sans', sans-serif;
}

.filter-container {
    display: flex;
    align-items: center;
    padding: 1.25rem;
    padding-left: 1.5rem;
    background-color: var(--dark-Blue-Dark-Mode-Elements);
    box-shadow: 0 1px 5px 0px rgba(0, 0, 0, .4);
    position: relative;
    border-radius: 5px;
    cursor: pointer;
    justify-content: space-between;
}

.light .filter-container,
.light .filter-box {
    background-color: var(--White);
}

.filter-box {
    position: absolute;
    width: 100%;
    top: 105%;
    left: 0;
    background-color: var(--dark-Blue-Dark-Mode-Elements);
    box-shadow: 0 1px 5px 0px rgba(0, 0, 0, .4);
    z-index: 5;
    border-radius: 5px;
    max-height: 0rem;
    overflow: hidden;
    transition: all .3s;
}

.show {
    max-height: 20rem;
}

.filter-items {
    padding: 1.25rem;
    padding-left: 1.5rem;
}

.filter-items p {
    margin-bottom: 0.5rem;
    cursor: pointer;
}

.filter-container p {
    color: var(--White);
}

.light .filter-container p,
.light .down-icon {
    color: black;
    font-weight: 600;
}

.down-icon {
    color: white;
    margin-left: 4rem;
    font-size: 1.25rem;
}

@media screen and (max-width: 600px) {
    .input-container {
        flex-direction: column;
    }
    .filter-container {
        width: 16rem;
        margin-top: 3rem;
    }
    .search {
        width: 100%;
    }
    .search input {
        width: 100%;
    }
}
.country-container {
    width: 20rem;
    height: 25rem;
    margin-right: 3rem;
    margin-bottom: 5rem;
    box-shadow: 0 0 5px 0px rgba(0, 0, 0, .5);
    text-decoration: none;
}

.flag-container,
.text-container {
    height: 50%;
    background-color: var(--dark-Blue-Dark-Mode-Elements);
}

.light .flag-container,
.light .text-container {
    background-color: var(--White);
}

.text-container {
    padding: 2rem;
    padding-bottom: 0;
}

.flag-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.text-container h1 {
    color: var(--White);
    font-size: 1.25rem;
    font-weight: 900;
    margin-bottom: 1rem;
}

.light .text-container h1,
.light .text-container p {
    color: #000;
}

.text-container p {
    color: white;
    font-weight: 300;
    line-height: 1.5rem;
}

.text-container span {
    font-weight: 700;
}

@media screen and (max-width:900px) {
    .country-container {
        width: 100%;
        height: 30rem;
        margin-right: 0;
    }
}
.countrylist-container {
    display: flex;
    flex-wrap: wrap;
}
*.hidden {
    display: none !important;
}

div.loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(16, 16, 16, 0.5);
}

@keyframes uil-ring-anim {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.uil-ring-css {
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 200px;
    height: 200px;
}

.uil-ring-css>div {
    position: absolute;
    display: block;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 80px;
    box-shadow: 0 6px 0 0 #ffffff;
    animation: uil-ring-anim 1s linear infinite;
}
.link-container {
    display: flex;
    flex-wrap: wrap;
    flex-basis: 60%;
    max-height: 15rem;
    overflow: auto;
}

.border-country {
    margin-bottom: .85rem;
}
section {
    padding: 0 6rem;
}

.back-container {
    padding: 4rem 0;
}

.light .back-container button {
    background-color: var(--White);
    color: black;
    font-weight: 700;
}

.back-container button {
    padding: .5rem 1.5rem;
    cursor: pointer;
    background-color: var(--dark-Blue-Dark-Mode-Elements);
    border: none;
    outline: none;
    color: white;
    display: flex;
    align-items: center;
    font-weight: 300;
    box-shadow: 0 0 5px 0px rgba(0, 0, 0, .5);
}

button .left-arrow {
    margin-right: .5rem;
    transform: translateY(15%);
    font-weight: 900;
}

.details {
    display: flex;
}

.flag {
    flex-basis: 50%;
    min-width: 30rem;
    max-width: 35rem;
    max-height: 25rem;
    margin-right: 5rem;
}

.detail {
    flex-basis: 50%;
}

.light .detail h1,
.light .block p {
    color: black;
}

.detail h1 {
    color: white;
    font-size: 2rem;
    font-weight: 800;
    margin-bottom: 2rem;
}

.block p {
    color: white;
    font-weight: 600;
    line-height: 1.85rem;
}

.block span {
    font-weight: 300;
    margin-left: .3rem;
}

.flag img {
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
}

.row {
    display: flex;
    flex-wrap: wrap;
}

.block:nth-of-type(1) {
    margin-right: 3rem;
    margin-bottom: 2rem;
}

.border {
    display: flex;
    margin-top: 2rem;
    align-items: baseline;
}

.light .detail h1,
.light .block p,
.light .border p {
    color: black;
}

.border p {
    color: white;
    font-weight: 600;
}

.light .border-country {
    background-color: var(--White);
    color: black;
    font-weight: 600;
}

.border-country {
    text-decoration: none;
    color: white;
    padding: .5rem .85rem;
    margin-left: .75rem;
    background-color: var(--dark-Blue-Dark-Mode-Elements);
    box-shadow: 0 0 5px 0px rgba(0, 0, 0, .5);
}

@media screen and (max-width: 600px) {
    section {
        padding: 0 2rem;
    }
    .border {
        flex-direction: column;
    }
    .border p {
        margin-bottom: 1rem;
    }
    .border-country {
        margin-left: 0;
        margin-right: .85rem;
    }
}

@media screen and (max-width: 900px) {
    .details {
        flex-direction: column;
    }
    .flag {
        width: 100%;
        max-width: 35rem;
        min-width: 0;
        min-height: 0;
    }
    .detail {
        flex-direction: column;
        margin-top: 4rem;
    }
    .row {
        flex-direction: column;
    }
}
